import { API_URL } from "./constants"

export async function search({ skip, limit, filter }) {
  const params = new URLSearchParams({
    skip: skip,
    limit: limit,
    filter: filter,
  })
  const response = await fetch(
    `${API_URL}/search?${params}`,
    {
      method: 'GET',
    }
  )
  const data = await response.json()
  return data
}