import { API_URL, MODE } from './constants'

const dummyAlbum = {
  mbid: '061e483c-7d6c-4864-ab94-546207952040',
  playcount: 7680067,
  image_link:
    'http://coverartarchive.org/release/061e483c-7d6c-4864-ab94-546207952040/20273297148-250.jpg',
  name: 'Harder Than Ever',
  tracks: [
    { link: '/track/2858', name: 'Cash' },
    { link: '/track/2790', name: 'Bank' },
    { link: '/track/2714', name: 'Leaked' },
    { link: '/track/2652', name: 'Yes Indeed' },
    { link: '/track/2557', name: 'Exotic' },
    { link: '/track/2480', name: "I'm Straight" },
    { link: '/track/2355', name: 'Spazz' },
    { link: '/track/823', name: 'Intro' },
  ],
  authors: [
    { link: '/track/2480', name: "I'm Straight" },
    { link: '/track/2355', name: 'Spazz' },
    { link: '/track/823', name: 'Intro' },],
}
export async function album({ id }) {
  if (MODE === 'dev') {
    return dummyAlbum
  }

  const response = await fetch(`${API_URL}/album/${id}`, {
    method: 'GET',
  })
  const data = await response.json()
  return data
}
