import { createBrowserRouter } from 'react-router-dom'
import App from '../App'
// import Recommended from '../views/Recommended'
import Login from '../views/Login'
// import Profile from '../views/Profile'
import Artist from '../views/Artist'
import RecommendedLikes from '../views/RecommendedLikes'
import RecommendedUser from '../views/RecommendedUser'
import Album from '../views/Album'
import Track from '../views/Track'

export const router = createBrowserRouter([
  {
    path: '/',
    element: <Login />,
  },
  {
    path: '/app',
    element: <App />,
    children: [
      {
        path: '/app/recommended-by-likes',
        element: <RecommendedLikes />,
      },
      {
        path: '/app/recommended-by-profile',
        element: <RecommendedUser />,
      },
      {
        path: '/app/artist/:id',
        element: <Artist />,
      },
      {
        path: '/app/album/:id',
        element: <Album />,
      },
      {
        path: '/app/track/:id',
        element: <Track />,
      },
    ],
  },
])
