import { API_URL, MODE } from './constants'

export async function artists({ skip, limit, filter }) {
  const params = new URLSearchParams({
    skip: skip,
    limit: limit,
    filter: filter,
  })
  const response = await fetch(`${API_URL}/artists?${params}`, {
    method: 'GET',
  })
  const data = await response.json()
  return data
}
