import './App.css'
import {
  Box,
  createTheme,
  CssBaseline,
  Icon,
  ThemeProvider,
  Typography,
  // Link as MuiLink,
  Tabs,
  Tab,
  AppBar,
  Toolbar,
  Button,
  Drawer,
  List,
  ListItem,
  Skeleton,
} from '@mui/material'

import './styles/toolbar.css'
import './styles/app-bar.css'
import './styles/profile.css'
import { useEffect, useState } from 'react'
import SearchInput from './components/SearchInput'
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom'
import api from './api'

function App() {

  const history = useLocation()
  console.log(history)

  const [user, setUser] = useState({})
  const [loadingUser, setLoadingUser] = useState(true)

  useEffect(() => {
    const getUser = async () => {
      setLoadingUser(true)
      const response = await api.user.me()
      setUser(response)
      setLoadingUser(false)
    }
    getUser()
  }, [])

  const [drawerOpen, setDrawerOpen] = useState(false)
  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen)
  }

  const navigate = useNavigate()
  const logout = async () => {
    setDrawerOpen(false)
    navigate('/')
    await api.auth.logout()
  }


  const theme = createTheme()
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar position="sticky" color="default" sx={{ top: 0 }}>
        <Toolbar sx={{p:0}}>
          <button onClick={toggleDrawer} className="profile-button">
            <Icon>person</Icon>
          </button>
          <SearchInput />
        </Toolbar>
      </AppBar>
      <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer}>
        <Box
          sx={{
            width: 250,
          }}
        >
          <Button onClick={toggleDrawer}>close</Button>
          <div className="profile-image-container">
            <img src={user.photo} alt="profile" className="profile-image" />
          </div>
          <List>
            <ListItem>
              {loadingUser && <Skeleton variant="text" width={100} />}
              {!loadingUser && (
                <Typography variant="h6">{`${user.first_name} ${user.last_name}`}</Typography>
              )}
            </ListItem>
            <ListItem>
              {loadingUser && <Skeleton variant="text" width={100} />}
              {!loadingUser && (
                <Typography variant="h6">{user.email}</Typography>
              )}
            </ListItem>
            <ListItem>
              <Button onClick={logout}>Log out</Button>
            </ListItem>
          </List>
        </Box>
      </Drawer>
      <main style={{ height: 0, minHeight: 'calc(100vh - 56px)' }}>
        <Tabs
          value={history.pathname}
          aria-label="icon tabs example"
          variant="fullWidth"
        >
          <Tab
            label="by similar user"
            value="/app/recommended-by-profile"
            component={Link}
            to="/app/recommended-by-profile"
            />
          <Tab
            label="by likes"
            component={Link}
            to="/app/recommended-by-likes"
            value="/app/recommended-by-likes"
            // disabled={loadingUser || user.likedArtists.length === 0}
          />
        </Tabs>
        <Outlet />
      </main>
    </ThemeProvider>
  )
}

export default App
