import { Button, CircularProgress, List, ListItem, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import api from '../api'



const RecommendedLikes = () => {
  const [results, setResults] = useState([])
  const [loading, setLoading] = useState(true)
  const [noLikes, setNoLikes] = useState(false)

  useEffect(() => {
    const getRecommended = async () => {
      setLoading(true)
      try {
        const response = await api.artistRecommendation.byLikes({skip: 0, limit: 20})
        setResults(response.data)
        setNoLikes(response.data.length===0)
      }
      catch (e) {
        setNoLikes(true)
      }
      setLoading(false)
    }
    getRecommended()
  }, [])
  const loadMore = async () => {
    const response = await api.artistRecommendation.byLikes({skip: results.length, limit: 20})
    setResults([...results, ...response.data])
    setNoLikes(false)
  }
  if (noLikes) {
    return <Typography>'Like' some artists so we can recommend you similar ones</Typography>
  }
  return (
    <List>
      {results.length!==0 && results.map((result) => {
        return (
          <ListItem key={result.name}>
            <Link to={`/app${result.link}`}>
            <Typography>{result.name}</Typography>
            </Link>
          </ListItem>
        )
      })}
      <ListItem >
        <Button onClick={loadMore}  disabled={loading}>
          Load More
          {loading && <CircularProgress size={20} sx={{ml:'8px'}} />}
        </Button>
      </ListItem>
    </List>
  )
}

export default RecommendedLikes