import { API_URL, MODE } from './constants'

const dummyTrack = {
  mbid: '94ca4d62-7d78-4568-99e8-9f72ae896049',
  playcount: 720617,
  name: 'Dare (La La La)',
  duration: 0,
  listeners: 109186,
  authors: [{ link: '/app/artist/570', name: 'Shakira' }],
}

export async function track({ id }) {
  if (MODE === 'dev') {
    return dummyTrack
  }
  const response = await fetch(`${API_URL}/track/${id}`, {
    method: 'GET',
  })
  const data = await response.json()
  return data
}
