import { Box, Link, Typography } from '@mui/material'
import React from 'react'
import { API_URL } from '../api/constants'
import loginBg from '../assets/login-bg.jpg'

const Login = () => {
  return (
    <Box
      sx={{
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'start',
        justifyContent: 'center',
        backgroundImage: `linear-gradient(to bottom, rgba(0,0,0, .2), rgba(0,0,0, .2)),  url(${loginBg})`,
        backgroundSize: 'cover',
        padding: '0 20px',
      }}
    >
      <Typography
        variant="h6"
        color="white"
        sx={{
          position: 'absolute',
          top: '20px',
          left: '20px',
          color: '#fffc',
        }}
      >
        Audite
      </Typography>
      <Typography variant="h3" gutterBottom color="white">
        Discover new music to your tastes
      </Typography>
      {/* // introduction */}
      <Typography variant="h6" gutterBottom color="#fffc" marginBottom="16px">
        Audite is a music recommendation app that uses your favorites to find
        new music you'll love.
      </Typography>

      <Link
        href={`${API_URL}/facebook`}
        sx={{
          padding: '10px 20px',
          backgroundColor: '#3b5998',
          color: 'white',
        }}
      >
        <b>Login with Facebook</b> to get started
      </Link>
    </Box>
  )
}

export default Login
