import { album } from './album'
import { artist, artists } from './artist'
import { logout } from './auth'
import { like } from './like'
import {
  recommendArtistsBasedOnLikes,
  recommendBasedOnUser,
} from './recommend'
import { search } from './search'
import { track } from './track'
import { me, user } from './user'

const api = {
  album: {
    single: album,
  },
  track: {
    single: track,
  },
  artist: {
    single: artist,
    all: artists,
  },
  search: search,
  like: like,
  artistRecommendation: {
    byLikes: recommendArtistsBasedOnLikes,
    byUser: recommendBasedOnUser,
  },
  auth: {
    logout: logout,
  },
  user: {
    single: user,
    me: me,
  },
}
export default api
