// display artist name, mbid, tracks and albums from url id

import { Box, Button, Container, Stack, Typography } from '@mui/material'
import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import api from '../api'

export default function Artist() {
  const { id } = useParams()
  const [artist, setArtist] = useState({})
  const [isLoading, setIsLoading] = useState(false)
  const [isLiked, setIsLiked] = useState(false)

  useEffect(() => {
    const getArtist = async () => {
      setIsLoading(true)
      const artist = await api.artist.single({ id })
      setArtist(artist)
      setIsLiked(artist.liked)
      setIsLoading(false)
    }
    getArtist()
  }, [id])

  const addToFavorites = async () => {
    await api.like({ label: 'artist', id, value: !isLiked })
    setIsLiked((prev) => !prev)
  }

  if (isLoading) {
    return <div>Loading...</div>
  }

  return (
    <Container sx={{ pb: '56px' }}>
      <Stack spacing={2}>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            mb: 1,
          }}
        >
          <Typography variant="h5" component="h1">
            {artist.name}
          </Typography>
          <Button onClick={addToFavorites} variant="contained">
            {isLiked ? 'Unlike' : 'Like'}
          </Button>
        </Box>
        <Box>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`MBID: ${artist.mbid}`}
          </Typography>
        </Box>
        <Box>
          <Typography variant="h6" component="h2">
            Albums
          </Typography>
          {artist.albums &&
            artist.albums.map((album) => (
              <Link to={`/app${album.link}`}>
                <Typography variant="body1" component="p">
                  {album.name}
                </Typography>
              </Link>
            ))}
        </Box>
        <Box>
          <Typography variant="h6" component="h2">
            Tracks
          </Typography>
          {artist.tracks &&
            artist.tracks.map((track) => (
              <Link to={`/app${track.link}`}>
                <Typography variant="body1" component="p">
                  {track.name}
                </Typography>
              </Link>
            ))}
        </Box>
      </Stack>
    </Container>
  )
}
