import { API_URL, MODE } from './constants'

export async function recommendArtistsBasedOnLikes({ skip, limit }) {
  const params = new URLSearchParams({
    skip: skip,
    limit: limit,
  })
  const response = await fetch(`${API_URL}/recommend-likes?${params}`, {
    method: 'GET',
  })
  const data = await response.json()
  return data
}
