export async function recommendBasedOnUser({ skip, limit }) {
  if (MODE === 'dev') {
    return dummyUserResults
  }
  const params = new URLSearchParams({
    skip: skip,
    limit: limit,
  })
  const response = await fetch(`${API_URL}/recommend-similar-user?${params}`, {
    method: 'GET',
  })
  const data = await response.json()
  return data
}
