// display artist name, mbid, tracks and albums from url id

import { Box, Container, Stack, Typography } from '@mui/material'
import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import api from '../api'

export default function Track() {
  const { id } = useParams()
  const [track, setTrack] = useState({})
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const getTrack = async () => {
      setIsLoading(true)
      const result = await api.track.single({ id })
      setTrack(result)
      setIsLoading(false)
    }
    getTrack()
  }, [id])

  if (isLoading) {
    return <div>Loading...</div>
  }

  return (
    <Container sx={{ pb: '56px' }}>
      <Stack spacing={2}>
        <Typography variant="h6" component="h2">
          Track
        </Typography>
        {/* <img src={track.image_link} alt={track.name} /> */}
        <Box>
          <Typography variant="h5" component="h1">
            {track.name}
          </Typography>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`MBID: ${track.mbid}`}
          </Typography>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`Duration: ${track.duration / 1000 / 60} minutes`}
          </Typography>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`Playcount: ${track.playcount}`}
          </Typography>
          {/* <Typography variant="body2" component="h2" color="text.secondary">
            {`Listeners: ${track.listeners}`}
          </Typography> */}
        </Box>
        <Box>
          <Typography variant="h6" component="h2">
            Authors
          </Typography>
          {!track.authors.length && 'N/A'}

          {track.authors.length &&
            track.authors.map((author) => (
              <Link to={`/app${author.link}`}>
                <Typography variant="body1" component="p">
                  {author.name}
                </Typography>
              </Link>
            ))}
        </Box>
      </Stack>
    </Container>
  )
}
