// display artist name, mbid, tracks and albums from url id

import { Box, Container, Stack, Typography } from '@mui/material'
import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import api from '../api'

export default function Album() {
  const { id } = useParams()
  const [album, setAlbum] = useState({})
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const getArtist = async () => {
      setIsLoading(true)
      const artist = await api.album.single({ id })
      setAlbum(artist)
      setIsLoading(false)
    }
    getArtist()
  }, [id])

  if (isLoading) {
    return <div>Loading...</div>
  }

  return (
    <Container sx={{pb:'56px'}}>
      <Stack spacing={2}>
        <Typography variant="h6" component="h2">
          Album
        </Typography>
        <img src={album.image_link} alt={album.name} />
        <Box>
          <Typography variant="h5" component="h1">
            {album.name}
          </Typography>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`MBID: ${album.mbid}`}
          </Typography>
          <Typography variant="body2" component="h2" color="text.secondary">
            {`Playcount: ${album.playcount}`}
          </Typography>
        </Box>
        <Box>
          <Typography variant="h6" component="h2">
            Authors
          </Typography>
          {!album.authors.length && 'N/A'}
          {album.authors &&
            album.authors.map((author) => (
              <Link to={`/app${author.link}`}>
                <Typography variant="body1" component="p">
                  {author.name}
                </Typography>
              </Link>
            ))}
        </Box>
        <Box>
          <Typography variant="h6" component="h2">
            Tracks
          </Typography>
          {album.tracks &&
            album.tracks.map((track) => (
              <Link to={`/app${track.link}`}>
                <Typography variant="body1" component="p">
                  {track.name}
                </Typography>
              </Link>
            ))}
        </Box>
      </Stack>
    </Container>
  )
}
