import { Button, CircularProgress, List, ListItem, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import api from '../api'



const RecommendedUser = () => {
  const [results, setResults] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const getRecommended = async () => {
      setLoading(true)
      const response = await api.artistRecommendation.byUser({skip: 0, limit: 20})
      setResults(response.data)
      setLoading(false)
    }
    getRecommended()
  }, [])
  const loadMore = async () => {
    const response = await api.artistRecommendation.byUser({skip: results.length, limit: 20})
    setResults([...results, ...response.data])
  }
  // if (loading) {
  //   return <Typography>Loading...</Typography>
  // }
  return (
    <List>
      {results.map((result) => {
        return (
          <ListItem key={result.name}>
            <Link to={`/app${result.link}`}>
            <Typography>{result.name}</Typography>
            </Link>
          </ListItem>
        )
      })}
      <ListItem >
        <Button onClick={loadMore}  disabled={loading}>
          Load More
          {loading && <CircularProgress size={20} sx={{ml:'8px'}} />}
        </Button>
      </ListItem>
    </List>
  )
}

export default RecommendedUser